import { useContext } from 'react';
import { ScrapeContext } from './scrapeContext';

const Data = () => {
  const { scrapes } = useContext(ScrapeContext)
  return (<div>
    <h2>Top 100 Profiles sorted by number of Followers</h2>
    <table>
      <thead>
        <tr>InstagramId</tr>
        <tr>Name</tr>
        <tr>Number of followers</tr>
        <tr>Dreister Follower?</tr>
      </thead>
      <tbody>
        {scrapes.map(scrape => (
          <tr key={scrape.instagramId}>
            <td>{scrape.instagramId}</td>
            <td>{scrape.instagramId}</td>
          </tr>
        ))}
      </tbody>
    </table>
  </div>)
}

export default Data;
